import { Component } from '@angular/core'

@Component({
    selector:'my-tutoril',
    template:`<h2>Awanish Tutorials</h2>
    {{title}}
    <p [ngClass]="{classOne:cone,classTwo:ctwo}">ngClass pragraph</p>
    <input type="button" value="click" (click)="showHide()">
    ` ,
    styles : [`.classOne{color:green}
                .classTwo{background-color:red}`]
})

export class TutorilComponent{
    public title='Welcome to tutorials';
    public cone=false;
    public ctwo=true;
showHide(){
    this.cone=!this.cone;
    this.ctwo=!this.ctwo;
    
}
}



